.modal {
   position: fixed;
   top: 0;
   left: 0;
   height: 100%;
   min-height: 100vh;
   width: 100%;
   background-color: rgba(0, 0, 0, 0.5);
   display: flex;
   justify-content: center;
   align-items: center;
   z-index: 20;
}
.modal_content {
   background-color: white;
   border-radius: 3px;
   max-height: 95vh;
   overflow-y: auto;
}
